<style scoped>

  .ivu-menu-dark {
    background-color: #004492 !important;
  }

  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
  }

  .layout-logo {
    margin-top: 5px;
    width: 300px;
    height: 50px;
    color: #fff;
    border-radius: 3px;
    float: left;
    position: relative;
    left: 20px;
    font-size: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
  }

  .layout-assistant {
    width: 300px;
    margin: 0 auto;
    height: inherit;
  }

  .logout {
    color: #fff;
    float: right;
    padding-right: 20px;
  }

  .layout-breadcrumb {
    padding: 10px 15px 0;
  }

  .layout-hide-text .layout-text {
    display: none;
  }

  .layout-content {
    min-height: 500px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }

  .layout-content-main {
    padding: 10px;
  }

  .layout-copy {
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }
</style>
<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft < 5}">
    <Menu mode="horizontal" theme="dark" :active-name="1">
      <div class="layout-logo">
         文件管理服务器
      </div>
    </Menu>
    <div class="layout-content">
      <Row type="flex">
        <i-col :span="spanLeft">
          <Menu @on-select="menuSelect" :active-name="active" width="auto" :open-names="['1']">
            <Submenu name="1">
              <template slot="title">
                <Icon type="ios-locked"></Icon>
                <span class="layout-text">文件管理</span>
              </template>
              <Menu-item name="1-1" class="layout-text">文件管理</Menu-item>
            </Submenu>
          </Menu>
        </i-col>
        <i-col :span="spanRight">

          <div class="layout-content-main">
            <router-view></router-view>
          </div>
        </i-col>
      </Row>
    </div>
    <div class="layout-copy">
      2017 &copy; www.anthinkingcoder.com
    </div>
    <Modal v-model="loading" width="200" :closable="false" :footerHide="true" :mask-closable="false">
      <Spin size="large">
        <Icon type="load-c" :size="36" class="spin-icon-load"></Icon>
        <div style="margin-top: 10px">加载中</div>
      </Spin>
    </Modal>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        loading: false,
        active: '1-1',
        spanLeft: 5,
        spanRight: 19,
        nav: {
          '1-1': {
            path: '/main/file',
            title: '文件管理'
          }
        }
      }
    },
    methods: {
      menuSelect (name) {
        this.$router.push({
          path: this.nav['' + name + ''].path
        })
        document.title = this.nav['' + name + ''].title
      }
    }
  }
</script>



