<style lang="stylus" ref="stylesheet/stylus">
</style>
<template>
  <div class="fileList">
    <div style="float: right;margin-top: 10px;margin-bottom: 10px">
      <Input v-model="search" style="width: 300px;" placeholder="请输入文件名">
      <Button slot="append" icon="ios-search" @click="listFiles(1)"></Button>
      </Input>
    </div>
    <div style="float: left;">
      <Table stripe :columns="columns" :data="files"></Table>
    </div>
    <div style="float: right;margin: 10px;overflow: hidden">
      <Page :total="total" :current="page" :page-size="per" @on-change="listFiles" show-total></Page>
    </div>
    <Modal v-model="loading" width="200" :closable="false" :footerHide="true" :mask-closable="false">
      <Spin size="large">
        <Icon type="load-c" :size="36" class="spin-icon-load"></Icon>
        <div style="margin-top: 10px">加载中</div>
      </Spin>
    </Modal>
    <div style="float: left;margin-top: 30px">
      <Upload
        multiple
        type="drag"
        action="http://116.196.123.97/upload" :on-success="successUpload">
        <div style="padding: 20px 0">
          <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
          <p>点击或将文件拖拽到这里上传</p>
        </div>
      </Upload>
    </div>
  </div>
</template>

<script>
  import qs from 'qs'

  export default {
    name: 'fileList',
    created () {
      this.listFiles(1)
    },
    data () {
      return {
        files: [],
        total: 0,
        per: 10,
        page: 1,
        loading: false,
        search: '',
        columns: [
          {
            title: '#',
            key: 'id'
          },
          {
            title: '文件名',
            key: 'name'
          },
          {
            title: 'md5',
            key: 'md5'
          },
          {
            title: 'preview',
            render: (h, params) => {
              return h('a', {
                attrs: {
                  href: params.row.path,
                  target: '_blank'
                },
                style: {
                  width: '100px',
                  height: '50px'
                }
              }, '详情')
            }
          },
          {
            title: '文件类型',
            key: 'contentType'
          },
          {
            title: '操作',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.removeFile(params.row.id)
                    }
                  }
                }, '删除'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.downloadFile(params.row.id)
                    }
                  }
                }, '下载')
              ])
            }
          }
        ]
      }
    },
    methods: {
      listFiles (page) {
        this.page = page - 1
        this.loading = true
        this.$http.get('/api/admin/file/all', {
          params: {
            size: this.per,
            page: this.page,
            search: this.search
          }
        }).then((response) => {
          this.loading = false
          let result = response.data
          let data = result.data
          if (result.code === 200) {
            this.files = data.content
            this.total = data.totalElements
            this.page = data.number + 1
          } else {
            this.$Message.error('服务器错误,请重试')
          }
        }).catch(() => {
          this.loading = false
          this.$Message.error('服务器错误,请重试')
        })
      },
      removeFile (id) {
        this.$Modal.confirm({
          title: '提示',
          content: '该操作不可逆,确认删除该文件',
          onOk: () => {
            this.loading = true
            this.$http.post('/api/admin/file/delete', qs.stringify({'id': id})).then((response) => {
              this.loading = false
              let result = response.data
              let data = result.data
              if (result.code === 200) {
                this.$Message.success('删除成功')
                this.listFiles(1)
              } else {
                this.$Message.error(data.message)
              }
            }).catch(() => {
              this.loading = false
              this.$Message.error('服务器错误,请重试')
            })
          }
        })
      },
      downloadFile (id) {
        location.href = 'http://116.196.123.97:8081/download/' + id
      },
      successUpload (response) {
        console.info(response)
        this.$Message.success('上传成功')
        this.listFiles(1)
      }
    }
  }
</script>





