import Vue from 'vue'
import Router from 'vue-router'
import Main from '../components/main.vue'
import File from '../components/file.vue'

Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/main',
      name: 'main',
      component: Main,
      children: [
        {
          path: '/main/file',
          component: File
        }
      ]
    }
  ]
})



